import { useCallback, useEffect, useRef, useState } from 'react'

export const useBlazorApp = () => {
  const intervalIdRef = useRef(0);

  const [blazorInstance, setBlazorInstance] = useState(window.Blazor);

  const [nextCounterIndex, setNextCounterIndex] = useState(1)
  const [blazorCounters, setBlazorCounters] = useState([])

  const initBlazorApp = useCallback(() => {
    const index = nextCounterIndex
    setNextCounterIndex(index + 1)
    setBlazorCounters(blazorCounters.concat([{
      title: `Counter ${index}`,
      incrementAmount: index,
      customObject: {
        StringValue: 'Hello!',
        IntegerValue: 42,
      },
    }]))
  }, [blazorCounters])

  const destroyBlazorApp = useCallback(() => {
    setBlazorCounters(blazorCounters.slice(0, -1))
  }, [blazorCounters])

  const handleBlazorInstanceChange = useCallback(() => {
    setBlazorInstance(window.Blazor)
  }, [])

  useEffect(() => {
    if (blazorInstance) {
      handleBlazorInstanceChange()
      return
    }

    const intervalId = setInterval(() => {
      if (window.Blazor) {
        handleBlazorInstanceChange()
        clearInterval(intervalId)
      }
    }, 500)

    intervalIdRef.current = intervalId

    return () => {
      destroyBlazorApp()
      clearInterval(intervalId)
    }
  }, [])

  useEffect(() => {
    if (!blazorInstance) {
      return
    }

    initBlazorApp()
  }, [blazorInstance])

  return {
    isBlazorInstanceReady: Boolean(blazorInstance),
    isBlazorAppReady: Boolean(blazorCounters.length),
  }
};
