import { useEffect } from 'react'
import { BlazorApp } from './vendor/Blazor/BlazorApp'
import { useBlazorApp } from './hooks/useBlazorApp'
import { trimDoubleSlashes } from './utils'

function App({
  baseUrl = '',
  url = '',
  blazorScripts = [],
  blazorStylesheets = [],
}) {
  const isLoadedFromFederativeApp = blazorScripts.length

  const {
    isBlazorInstanceReady,
    isBlazorAppReady,
  } = useBlazorApp()

  useEffect(() => {
    if (!isLoadedFromFederativeApp) {
      return
    }

    const baseElement = document.createElement('base')
    baseElement.href = `${trimDoubleSlashes(`${baseUrl}${url}`)}/`

    const scripts = blazorScripts.map((scriptUrl) => {
      const scriptElement = document.createElement('script')
      scriptElement.src = trimDoubleSlashes(`${baseUrl}${url}/${scriptUrl}`)
      scriptElement.async = true

      return scriptElement
    })

    const stylesheets = blazorStylesheets.map((stylesheetUrl) => {
      const linkElement = document.createElement('link')
      linkElement.href = trimDoubleSlashes(`${baseUrl}${url}/${stylesheetUrl}`)
      linkElement.rel = 'stylesheet'

      return linkElement
    })

    document.head.appendChild(baseElement)

    if (!isBlazorInstanceReady) {
      scripts.forEach((script) => {
        document.body.appendChild(script)
      })
      stylesheets.forEach((stylesheet) => {
        document.head.appendChild(stylesheet)
      })
    }
    return () => {
      document.head?.removeChild(baseElement)
    }
  }, [])

  // Без обертки в html элемент <div> unmount происходит с задержкой. Видимо, это требование Blazor приложения
  return (
      <div>
        {isBlazorAppReady
            ? (
                <BlazorApp />
            ) : 'Идет загрузка приложения...'
        }
      </div>
  );
}

export default App;
